.Contact {
    background-color: black;
    height: 10vh;
    display:flex;
    align-items: center;
    width: 100vw;
    bottom:0%;
}

.Contact .ContactIcons {
    width:100%;
    display:flex;
    justify-content: center;
    

}
.Contact .ContactIcons div {
    padding:5%;
}


/* Make react icons pointers!!! */

.Contact .ContactIcons svg path {
    cursor: pointer;
}