.LinksContainer {
    
    margin-top: 0%;
}


.LinksContainer a {
    display:block;
    text-align: center;
    text-decoration: none;
    border-radius: 10px;
    background-color: black;
    cursor: pointer;
    box-shadow:0px 0px 5px 1px white; 
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 2vw;
    padding-right: 2vw;
    margin: 0 auto 10px auto;
    max-width: 100%;
    max-height: 50%;
    
}


