.FlexTest{
    
    display:flex;
    
    width: 90vw;
    justify-content: center;
    
    margin-bottom: 10px;
    
    
      
}

.FlexTest .name {
    display: flex;
    flex-direction: column;
    
    align-items: center;
    width: 100%;
    height:100%;
    
}

.FlexTest .SPD {
    width:40%;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-right: 3%;
   
     
}

.FlexTest .SPD img {
    max-height: 100%;
    max-width: 100%;
    padding-bottom: 5%;
   
    
}

.FlexTest .gryffin {
    width: 40%;
    display: flex;
    flex-direction: column;
    position: relative;
    
}

.FlexTest .gryffin img {
    max-height: 100%;
    max-width: 100%;

}

.FlexTest .SPD a{
    text-decoration: none;
    border-radius: 10px;
    display:flex;
    background-color: white;
    box-shadow:0px 0px 10px 1px white;
    width: 100%;
    height: 100%;
    
}

.FlexTest .gryffin a{
    text-decoration: none;
    border-radius: 10px;
    display:flex;
   
    background-color: white;
    box-shadow:0px 0px 10px 1px white;
    width: 100%;
    height:100%;
    
}

.FlexTest .SPD .name p{
    color: black;
    font-weight: 500;
    font-size: 125%;
}
.FlexTest .gryffin .name p{
    color: black;
    font-weight: 500;
    font-size: 125%;
}

