.Sponsors {
    background-color: blue;
  
    text-align: center;
   
}

.Sponsors .FlexContainer .Sponsor {
    display: flex;
    align-items: center;
    background-color: blueviolet;
    position: absolute;

}

.Sponsors .FlexContainer .Sponsor .sponsorText {
    width:80%;
    
}

.Sponsors .FlexContainer .Sponsor .thumbnail {
        padding-left: 2.5%;
        padding-right: 2.5%;
        padding-top: 2.5%;
        padding-bottom: 2.5%;
        position:relative;
        height:80%;
        width:80%;
}


